<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
import utils from './utils/utils.js'
export default {
  name: 'App',
  created () {
    if (utils.is_weixn()) {
      Toast({
        message: '建议点击右上角，使用其他浏览器打开，可以直接唤醒淘宝',
        position: 'top',
        duration: 5000
      })
    }
  }
}
</script>

<style>
html,
body,
#app,
.index,
.view {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.view > div {
  height: 100%;
  overflow: auto;
}
* {
  box-sizing: border-box;
}
img {
  width: 100%;
}
#app {
  background: #f0f0f0;
}
.flex3,
.flex4 {
  font-size: 0;
}
.flex3 > * {
  width: 33.3333%;
}
.flex4 > * {
  width: 25%;
}
.mint-header {
  background: #f96153;
}
.ltr {
  overflow: hidden;
}
.ltr > div:nth-child(odd) {
  margin-left: 5px;
  float: left;
}
.ltr > div:nth-child(even) {
  margin-right: 5px;
  float: right;
}
</style>



